// app/cart/page.tsx
// Version: 3.0.0 - 2025-11-10 - COMPLETE IMAGE & DETAILS FIX
// ‚úÖ Fixed: Shows BOTH masked preview AND original uploaded image
// ‚úÖ Fixed: Product images for non-engraved items (light bases, etc)
// ‚úÖ Fixed: Complete product details display
// ‚úÖ Fixed: Full-res images stored separately in sessionStorage
// ‚úÖ Enhanced: Better visual hierarchy and organization
// Previous: Only showed compressed preview image

'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'

interface CartItem {
  productId: string
  name: string
  sku: string
  price: number
  quantity: number
  options: {
    size?: string
    background?: string
    lightBase?: string
    giftStand?: string
    customText?: string | { line1?: string; line2?: string }
    rawImageUrl?: string
    imageUrl?: string
    maskedImageUrl?: string
    imageFilename?: string
    maskName?: string
  }
  sizeDetails?: {
    name: string
    price: number
  }
  customImage?: {
    dataUrl: string          // Compressed thumbnail preview
    filename: string
    mimeType: string
    fileSize: number
    width: number
    height: number
    fullResId?: string       // ID to retrieve full-res image from sessionStorage
  }
  cockpit3d_id?: string
  dateAdded: string
}

export default function CartPage() {
  const [cart, setCart] = useState<CartItem[]>([])
  const [total, setTotal] = useState(0)
  const [loading, setLoading] = useState(true)
  const [checkoutLoading, setCheckoutLoading] = useState(false)

  useEffect(() => {
    loadCart()
  }, [])

  function loadCart() {
    const cartData = JSON.parse(localStorage.getItem('cart') || '[]')
    
    console.log('üõí Cart loaded:', {
      itemCount: cartData.length,
      items: cartData.map((item: any, i: number) => ({
        index: i,
        name: item.name,
        hasCustomImage: !!item.customImage,
        hasOptionsImages: !!(item.options?.rawImageUrl || item.options?.imageUrl),
        cockpit3d_id: item.cockpit3d_id
      }))
    })
    
    setCart(cartData)
    
    const sum = cartData.reduce((acc: number, item: CartItem) => {
      const itemPrice = item.price || (item as any).totalPrice || 0
      return acc + (itemPrice * item.quantity)
    }, 0)
    setTotal(sum)
    setLoading(false)
  }

  function removeItem(index: number) {
    const newCart = cart.filter((_, i) => i !== index)
    setCart(newCart)
    localStorage.setItem('cart', JSON.stringify(newCart))
    
    const sum = newCart.reduce((acc, item) => {
      const itemPrice = item.price || (item as any).totalPrice || 0
      return acc + (itemPrice * item.quantity)
    }, 0)
    setTotal(sum)
    
    window.dispatchEvent(new Event('cartUpdated'))
  }

  function updateQuantity(index: number, newQuantity: number) {
    if (newQuantity < 1) return
    
    const newCart = [...cart]
    newCart[index].quantity = newQuantity
    setCart(newCart)
    localStorage.setItem('cart', JSON.stringify(newCart))
    
    const sum = newCart.reduce((acc, item) => {
      const itemPrice = item.price || (item as any).totalPrice || 0
      return acc + (itemPrice * item.quantity)
    }, 0)
    setTotal(sum)
    
    window.dispatchEvent(new Event('cartUpdated'))
  }

  function clearCart() {
    if (!window.confirm('Are you sure you want to clear your cart?')) return
    
    setCart([])
    setTotal(0)
    localStorage.removeItem('cart')
    window.dispatchEvent(new Event('cartUpdated'))
  }

  // Build Cockpit3D order structure
  function buildCockpitOrder(): any {
    const orderNumber = `CK-${Date.now()}`
    
    return {
      order_id: orderNumber,
      customer_name: '',
      email: '',
      phone: '',
      staff_user: 'Web Order',
      shipping_method: 'air',
      destination: 'customer_home',
      shipping_address: {},
      billing_address: {},
      items: cart.map(item => {
        // Try to get full-resolution image from sessionStorage
        const fullResImage = item.customImage?.fullResId 
          ? sessionStorage.getItem(`fullimg_${item.customImage.fullResId}`)
          : null
        
        return {
          sku: item.sku || item.productId,
          qty: item.quantity,
          price: item.price || (item as any).totalPrice || 0,
          name: item.name,
          custom_options: buildCustomOptions(item),
          custom_image_url: fullResImage || item.customImage?.dataUrl
          // custom_image_url: fullResImage || item.customImage?.dataUrl || item.options.rawImageUrl || item.options.imageUrl || undefined
        }
      }),
      total: total
    }
  }

  function buildCustomOptions(item: CartItem) {
    const options: Array<{ option_id: string; option_value: string }> = []
    
    const getOptionValue = (option: any): string => {
      if (!option) return ''
      if (typeof option === 'string') return option
      if (typeof option === 'object') return option.name || option.id || String(option)
      return String(option)
    }
    
    if (item.options.size) {
      options.push({
        option_id: 'size',
        option_value: item.sizeDetails?.name || getOptionValue(item.options.size)
      })
    }
    
    if (item.options.background) {
      options.push({
        option_id: 'background',
        option_value: getOptionValue(item.options.background)
      })
    }
    
    if (item.options.lightBase) {
      options.push({
        option_id: 'lightBase',
        option_value: getOptionValue(item.options.lightBase)
      })
    }
    
    if (item.options.giftStand) {
      options.push({
        option_id: 'giftStand',
        option_value: getOptionValue(item.options.giftStand)
      })
    }
    
    if (item.options.customText) {
      const textValue = typeof item.options.customText === 'string' 
        ? item.options.customText
        : `${item.options.customText.line1 || ''}\n${item.options.customText.line2 || ''}`.trim()
      
      options.push({
        option_id: 'customText',
        option_value: textValue
      })
    }
    
    return options
  }

  async function proceedToCheckout() {
    console.log('üéØ Proceeding to checkout...')
    setCheckoutLoading(true)

    try {
       const orderData = buildCockpitOrder()

      const cartData = JSON.parse(localStorage.getItem('cart') || '[]')
      // optional: persist for the checkout page to read
      sessionStorage.setItem('pendingCart', JSON.stringify(cartData))
      window.location.href = '/checkout'
    } catch (err) {
      console.error('Checkout error:', err)
      alert('Checkout failed. Please try again.')
    }
  }

  // async function proceedToCheckout() {
  //   console.log('üéØ Proceeding to checkout...')
  //   setCheckoutLoading(true)
    
  //   try {
  //     const orderData = buildCockpitOrder()
      
  //     console.log('üì¶ Order being sent to Stripe:', orderData)
      
  //    const response = await fetch('/api/create-checkout-session', {
  //       method: 'POST',
  //       headers: { 'Content-Type': 'application/json' },
  //       body: JSON.stringify({
  //         cartItems: cart.map(item => ({
  //           name: item.name,
  //           sku: item.sku,
  //           price: item.price,
  //           quantity: item.quantity
  //         })),
  //         orderNumber: `CK-${Date.now()}`
  //       })
  //     })
      
  //     if (!response.ok) {
  //       const errorText = await response.text()
  //       throw new Error(`Checkout failed: ${errorText}`)
  //     }
      
  //     const { url } = await response.json()
      
  //     if (url) {
  //       window.location.href = url
  //     } else {
  //       throw new Error('No checkout URL received')
  //     }
      
  //   } catch (error) {
  //     console.error('‚ùå Checkout error:', error)
  //     alert('Checkout failed. Please try again.')
  //     setCheckoutLoading(false)
  //   }
  // }

  if (loading) {
    return (
      <div className="min-h-screen bg-slate-50 flex items-center justify-center">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
          <p className="mt-4 text-slate-600">Loading your cart...</p>
        </div>
      </div>
    )
  }

  if (cart.length === 0) {
    return (
      <div className="min-h-screen bg-slate-50 flex items-center justify-center">
        <div className="text-center p-8">
          <svg className="mx-auto h-16 w-16 text-gray-400 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z" />
          </svg>
          <h2 className="mt-4 text-xl font-semibold text-slate-900">Your cart is empty</h2>
          <p className="mt-2 text-slate-600">Add some items to get started!</p>
          <Link 
            href="/products"
            className="mt-6 inline-block px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-medium rounded-md transition-colors"
          >
            Browse Products
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <div className="max-w-7xl max-lg:max-w-4xl mx-auto p-6">
        <div className="flex items-center justify-between mb-6 text-slate-900">
          <h2 className="text-xl font-semibold text-slate-900">Your Shopping Cart</h2>
          <button
            onClick={clearCart}
            className="text-sm text-red-600 hover:text-red-700 font-medium"
          >
            Clear Cart
          </button>
        </div>

        <div className="grid lg:grid-cols-3 gap-4 relative">
          {/* Cart Items Column */}
          <div className="lg:col-span-2 space-y-4">
            {cart.map((item, index) => (
              <div key={index} className="flex gap-6 p-6 bg-white shadow-sm border border-gray-300 rounded-md">
                
                {/* Images Section - Shows ALL available images */}
                <div className="shrink-0 flex flex-col gap-3 w-48 text-slate-900">
                                
                  {/* Customer preview (masked or compressed) */}
                  {!!item.customImage?.dataUrl && (
                    <div className="relative">                      
                      <img
                        src={item.customImage.dataUrl}
                        alt={`${item.name} preview`}
                        className="w-48 h-48 rounded-lg object-contain bg-gray-50 border-2 border-emerald-500 shadow-sm"
                      />

                      <span className="absolute top-1 right-1 bg-emerald-600 text-white text-[10px] px-1.5 py-0.5 rounded">
                        PREVIEW
                      </span>
                    </div>
                  )}

                  {/* Customer original if present in options */}
                  {!!item.options?.rawImageUrl && (
                    <div className="relative">
                      customer image
                      <img
                        src={item.options.rawImageUrl}
                        alt={`${item.name} original`}
                        className="w-48 h-48 rounded-lg object-contain bg-gray-50 border border-gray-300"
                      />
                      <span className="absolute top-1 right-1 bg-emerald-600 text-white text-[10px] px-1.5 py-0.5 rounded">
                        UPLOADED
                      </span>
                    </div>
                  )}
            

                  {/* Product image fallback for non-engraved items like light bases */}
                  {!item.customImage?.dataUrl &&
                   !item.options?.rawImageUrl &&
                   !item.options?.imageUrl && (
                    <div className="relative">
                      <img
                        src={
                          item.cockpit3d_id
                            ? `/img/products/cockpit3d/${item.cockpit3d_id}/cockpit3d_${item.cockpit3d_id}_${item.sku}.jpg`
                            : `/img/products/${item.sku || item.productId}.jpg`
                        }
                        onError={(e) => {
                          const img = e.currentTarget
                          if (!img.dataset.step && item.cockpit3d_id) {
                            img.dataset.step = 'png'
                            img.src = `/img/products/cockpit3d/${item.cockpit3d_id}/cockpit3d_${item.cockpit3d_id}_${item.sku}.png`
                          } else {
                            img.src = 'https://placehold.co/800x800?text=No+Image'
                          }
                        }}
                        alt={item.name}
                        className="w-48 h-48 rounded-lg object-contain bg-gray-50 border border-gray-200"
                      />
                    </div>
                  )}
 
    
                  {/* Fallback: No image available */}
                  {!item.customImage?.dataUrl && !item.options?.rawImageUrl && !item.options?.imageUrl && !item.cockpit3d_id && (
                    <div className="w-32 h-32 rounded-lg bg-gray-100 flex items-center justify-center text-gray-400 text-xs text-center p-2 border border-gray-200">
                      No Image<br/>Available
                    </div>
                  )}
                </div>

                {/* Product Details Section */}
                <div className="flex-1">
                  <div className="flex justify-between items-start mb-3 text-gray-900">
                    <h3 className="text-lg font-semibold text-gray-900">{item.name}</h3>
                    
                    {/* Remove Button */}
                    <button
                      onClick={() => removeItem(index)}
                      className="text-red-500 hover:text-red-700 transition-colors cursor-pointer"
                      title="Remove item"
                    >
                      <svg 
                        xmlns="http://www.w3.org/2000/svg" 
                        className="w-5 h-5" 
                        viewBox="0 0 320.591 320.591"
                        fill="currentColor"
                      >
                        <path d="M30.391 318.583a30.37 30.37 0 0 1-21.56-7.288c-11.774-11.844-11.774-30.973 0-42.817L266.643 10.665c12.246-11.459 31.462-10.822 42.921 1.424 10.362 11.074 10.966 28.095 1.414 39.875L51.647 311.295a30.366 30.366 0 0 1-21.256 7.288z"></path>
                        <path d="M287.9 318.583a30.37 30.37 0 0 1-21.257-8.806L8.83 51.963C-2.078 39.225-.595 20.055 12.143 9.146c11.369-9.736 28.136-9.736 39.504 0l259.331 257.813c12.243 11.462 12.876 30.679 1.414 42.922-.456.487-.927.958-1.414 1.414a30.368 30.368 0 0 1-23.078 7.288z"></path>
                      </svg>
                    </button>
                  </div>

                  {/* Product Specifications */}
                  <ul className="text-sm text-slate-600 space-y-1.5 mb-4">
                    {item.productId && (
                      <li><span className="font-semibold">Product ID:</span> {item.productId}</li>
                    )}
                    {item.sku && (
                      <li><span className="font-semibold">SKU:</span> {item.sku}</li>
                    )}
                    {item.cockpit3d_id && (
                      <li><span className="font-semibold">Cockpit3D ID:</span> {item.cockpit3d_id}</li>
                    )}
                    {item.sizeDetails && (
                      <li><span className="font-semibold">Size:</span> {item.sizeDetails.name || 'N/A'}</li>
                    )}
                    {item.options.background && (
                      <li>
                        <span className="font-semibold">Background:</span>{' '}
                        {typeof item.options.background === 'object' 
                          ? (item.options.background as any).name || (item.options.background as any).id
                          : item.options.background}
                      </li>
                    )}
                    {item.options.lightBase && (
                      <li>
                        <span className="font-semibold">Light Base:</span>{' '}
                        {typeof item.options.lightBase === 'object'
                          ? (item.options.lightBase as any).name || (item.options.lightBase as any).id
                          : item.options.lightBase}
                      </li>
                    )}
                    {item.options.giftStand && (
                      <li>
                        <span className="font-semibold">Stand:</span>{' '}
                        {typeof item.options.giftStand === 'object'
                          ? (item.options.giftStand as any).name || (item.options.giftStand as any).id
                          : item.options.giftStand}
                      </li>
                    )}
                    {item.options.customText && (
                      <li className="text-slate-700">
                        <span className="font-semibold">Custom Text:</span>{' '}
                        {typeof item.options.customText === 'string' ? (
                          item.options.customText
                        ) : (
                          <>
                            {item.options.customText.line1 && <span>{item.options.customText.line1}</span>}
                            {item.options.customText.line2 && <span> | {item.options.customText.line2}</span>}
                          </>
                        )}
                      </li>
                    )}
                    {item.customImage?.filename && (
                      <li className="text-emerald-600">
                        <span className="font-semibold">‚úì Custom Image:</span> {item.customImage.filename}
                      </li>
                    )}
                  </ul>

                  <hr className="border-gray-200 my-3" />

                  {/* Quantity and Price Controls */}
                  <div className="flex items-center justify-between flex-wrap gap-4">
                    <div className="flex items-center gap-3 text-slate-900">
                      <span className="text-sm font-semibold text-slate-900">Qty:</span>
                      <button 
                        type="button"
                        onClick={() => updateQuantity(index, item.quantity - 1)}
                        className="flex items-center justify-center w-5 h-5 bg-blue-500 hover:bg-blue-600 rounded transition-colors cursor-pointer"
                        disabled={item.quantity <= 1}
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="w-3 fill-white" viewBox="0 0 124 124">
                          <path d="M112 50H12C5.4 50 0 55.4 0 62s5.4 12 12 12h100c6.6 0 12-5.4 12-12s-5.4-12-12-12z"></path>
                        </svg>
                      </button>
                      <span className="font-semibold text-base min-w-[20px] text-center">{item.quantity}</span>
                      <button 
                        type="button"
                        onClick={() => updateQuantity(index, item.quantity + 1)}
                        className="flex items-center justify-center w-5 h-5 bg-blue-500 hover:bg-blue-600 rounded transition-colors cursor-pointer"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="w-3 fill-white" viewBox="0 0 42 42">
                          <path d="M37.059 16H26V4.941C26 2.224 23.718 0 21 0s-5 2.224-5 4.941V16H4.941C2.224 16 0 18.282 0 21s2.224 5 4.941 5H16v11.059C16 39.776 18.282 42 21 42s5-2.224 5-4.941V26h11.059C39.776 26 42 23.718 42 21s-2.224-5-4.941-5z"></path>
                        </svg>
                      </button>
                    </div>
                    
                    <div className="text-lg font-bold text-slate-900">
                      ${((item.price || (item as any).totalPrice || 0) * item.quantity).toFixed(2)}
                    </div>
                  </div>

                  {/* Development Debug Info */}
                  {process.env.NODE_ENV === 'development' && (
                    <div className="mt-3 p-2 rounded bg-blue-50 border border-blue-200">
                      <details className="text-xs">
                        <summary className="text-blue-700 font-semibold cursor-pointer">Debug Info</summary>
                        <div className="text-blue-600 mt-1 space-y-0.5">
                          <div>Has CustomImage: {item.customImage?.dataUrl ? '‚úì Yes' : '‚úó No'}</div>
                          <div>Has rawImageUrl: {item.options?.rawImageUrl ? '‚úì Yes' : '‚úó No'}</div>
                          <div>Has imageUrl: {item.options?.imageUrl ? '‚úì Yes' : '‚úó No'}</div>
                          {item.customImage && (
                            <div>Preview Size: {(item.customImage.dataUrl.length / 1024).toFixed(1)}KB</div>
                          )}
                        </div>
                      </details>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>

          {/* Order Summary Column - Sticky */}
          <div className="text-slate-900 bg-white h-max rounded-md p-6 shadow-sm border border-gray-300 sticky top-4">
            <h3 className="text-lg font-semibold text-slate-900 mb-4">Order Summary</h3>
            
            <ul className="text-slate-600 text-sm divide-y divide-gray-200">
              <li className="flex justify-between py-3">
                <span>Subtotal ({cart.reduce((sum, item) => sum + item.quantity, 0)} items)</span>
                <span className="font-semibold text-slate-900">${total.toFixed(2)}</span>
              </li>
              <li className="flex justify-between py-3">
                <span>Shipping</span>
                <span className="font-medium text-slate-700">Calculated at checkout</span>
              </li>
              <li className="flex justify-between py-3">
                <span>Tax</span>
                <span className="font-medium text-slate-700">Calculated at checkout</span>
              </li>
              <li className="flex justify-between py-3 font-bold text-base text-slate-900">
                <span>Total</span>
                <span>${total.toFixed(2)}</span>
              </li>
            </ul>

            <button 
              type="button" 
              onClick={proceedToCheckout}
              disabled={checkoutLoading}
              className="mt-6 w-full px-4 py-3 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-md transition-colors disabled:opacity-50 disabled:cursor-not-allowed shadow-sm"
            >
              {checkoutLoading ? (
                <span className="flex items-center justify-center">
                  <svg className="animate-spin -ml-1 mr-2 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Processing...
                </span>
              ) : (
                'Proceed to Checkout'
              )}
            </button>

            <p className="text-center text-slate-500 text-xs mt-4">
              üîí Secure checkout powered by Stripe
            </p>
          </div>
        </div>

        {/* Development Debug Section */}
        {process.env.NODE_ENV === 'development' && (
          <div className="mt-8 pt-6 border-t border-gray-300">
            <h6 className="text-amber-600 font-semibold mb-3 text-sm">üîß Developer Mode</h6>
            
            <div className="bg-slate-900 p-4 rounded-lg mb-3">
              <h6 className="text-slate-300 text-sm mb-2 font-semibold">üì¶ Cockpit3D Order Structure</h6>
              <pre className="text-slate-100 text-xs overflow-auto max-h-80 font-mono">
                {JSON.stringify(buildCockpitOrder(), null, 2)}
              </pre>
            </div>

            <div className="bg-slate-900 p-4 rounded-lg">
              <h6 className="text-slate-300 text-sm mb-2 font-semibold">üõí Raw Cart Data</h6>
              <pre className="text-slate-100 text-xs overflow-auto max-h-80 font-mono">
                {JSON.stringify(cart, null, 2)}
              </pre>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}